import { createRouter,createWebHashHistory } from "vue-router";
import learn from '../views/learn.vue'
import dfs from '../components/learn/learn_alg/1_graph/dfs.vue'
import bfs from "@/components/learn/learn_alg/1_graph/bfs.vue";
import treeDfs from "@/components/learn/learn_alg/2_tree/treeDfs.vue";
import arrayNodes from "@/components/learn/learn_alg/4_linkedNode/arrayNodes.vue";
import heap_ops from "@/components/learn/learn_alg/5_heap/heap_ops.vue";
import ij from "@/components/learn/learn_alg/alg/ij.vue";
import stringHash from "@/components/learn/learn_alg/alg/stringHash.vue";
import trieTree from "@/components/learn/learn_alg/alg/trieTree.vue";
import unionFind from "@/components/learn/learn_alg/alg/unionFind.vue";
import window from "@/components/learn/learn_alg/alg/window.vue";
import quicksort from "@/components/learn/learn_alg/3_sort/quicksort.vue"
import mergesort from "@/components/learn/learn_alg/3_sort/mergesort.vue";
import vector from "@/components/learn/toolAlgs/vector.vue";
import deque from "@/components/learn/toolAlgs/deque.vue";
import map from "@/components/learn/toolAlgs/map.vue";
import memset from "@/components/learn/toolAlgs/memset.vue";
import pair from "@/components/learn/toolAlgs/pair.vue"
import set from "@/components/learn/toolAlgs/set.vue"
import second from "@/views/second.vue";

const routes = [
    {path:'/learn',component:learn,children:[{path:'',component:dfs},{path:'dfs',component:dfs},
    {path:'mergesort',component:mergesort},{path:'quicksort',component:quicksort},
    {path:'bfs',component:bfs},{path:'treeDfs',component:treeDfs},
    {path:'arrayNodes',component:arrayNodes},{path:'heap_ops',component:heap_ops},
    {path:'ij',component:ij},{path:'stringHash',component:stringHash},
    {path:'trieTree',component:trieTree},{path:'unionFind',component:unionFind},
    {path:'window',component:window},{path:'vector',component:vector},
    {path:'deque',component:deque},{path:'memset',component:memset},
    {path:'pair',component:pair},{path:'map',component:map},
    {path:'set',component:set}
    ,{path:'unionFind',component:unionFind},
    // {path:'trieTree',component:trieTree},{path:'unionFind',component:unionFind},
    // {path:'trieTree',component:trieTree},{path:'unionFind',component:unionFind},
    // {path:'trieTree',component:trieTree},{path:'unionFind',component:unionFind},
        // ,
				// {
				// 	name:'qita',
				// 	path:'qita',
				// 	component:qita,
					
				// }
      ]},
      {path:'/second',component:second,children:[{path:'',component:dfs}]},
    {
      path:'/',
      redirect:'/home'
    }
  ]
  const router = createRouter({
    history:createWebHashHistory(),
    routes
  })
  export default router
